.viz3 {
  width: 100%;
  height: 100vh;
  background-color: #FFF8DC; 
  display: flex;
  justify-content: center;
}

.viz3-header {
  background-color: #FFF8DC;
  justify-content: center;
  align-items: center;
  padding-top: 1rem;
}

.viz3-tooltip {
  position: absolute;
  pointer-events: none;
  background: white;
  border: 1px solid #ccc;
  padding: 6px 10px;
  border-radius: 4px;
  font-size: 12px;
  box-shadow: 0 2px 6px rgba(0,0,0,0.15);
}
